.left {
    float: left;
    position: inherit;
    height: 60px;
    position: inherit;
    margin: -165px 0px 0px 20px;
}

.right {
    float: right;
    position: inherit;
    height: 100px;
    position: inherit;
    margin: 15px 0px 0px 20px;
}

.logo {
    float: left;
    position: inherit;
    height: 77px;
    position: inherit;
    margin: 35px 0px 0px 20px;
}

.signup {
    float: right;
    position: inherit;
    height: 100px;
    position: inherit;
    margin: 15px 0px 0px 20px;
}


.App-header {
    background-color: black;
    height: 150px;
    padding: 20px;
    color: white;
}

/* Add a black background color to the top navigation */
.topnav {
    background-color: black;
    overflow: hidden;
}

.footer {
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    color: white;
    text-align: center;
}

.icon {
    padding: 10px 20px 20px 10px;
    height: 50px;
    width: 50px;
}


/* Style the links inside the navigation bar */
.topnav a {
    color: #f2f2f2;
    text-align: center;
    margin: 0 20px;
    padding: 4px 3px;
    text-decoration: none;
    font-size: 30px;
}

/* Change the color of links on hover */
.topnav a:hover {
    background-color: #ddd;
    color: black;
}

/* Add a color to the active/current link */
.topnav a.active {
    color: black;
    background-color: #f2f2f2;
}

.App-header {
    background-color: black;
    height: 150px;
    padding: 20px;
    color: white;
}

.App-header-mobile {
    background-color: black;
    height: 150px;
    padding: 20px;
    color: white;
}


@media only screen and (max-width: 600px) {
    .logo {
        float: none;
        position: inherit;
        height: 110px;
        position: inherit;
        margin: 35px 0px 0px 100px;
    }
    .signup {
        float: right;
        position: inherit;
        height: 65px;
        position: inherit;
        margin: 0px 0px 0px 0px;
    }
    .App-title {
        font-size:1.5em;
    }
}




